@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-color: #f4f4f4; */
  color: #3b3b3c;
  font-family: 'Source Sans Pro', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #f4f4f4;
  min-width: 100%;
  min-height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #3b3b3c;
  font-size: 12px;
}

.col {
  min-width: 400px;
  text-align: left;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button {
  min-height: 20px;
  align-self: end;
}


.popup-content {
  z-index: 4000;
  position: relative;
}

.card {
  text-align: left;
  position: relative;
}

.content {
  padding: 5px 10px;
}

.popUp {
  border-radius: 10px;
  padding: 0px 7px;
  height: 5px;
  line-height: 0.7em;
  font-style: italic;
  background-color: #00000000;
}





.BodyComponent .leftCol {
  text-align: right; }

.BodyComponent .rightCol {
  text-align: left; }

.BodyComponent .filterButton {
  border-radius: 4px;
  padding: 0px 10px;
  height: 3em;
  margin: 0px 5px;
  vertical-align: center; }

.BodyComponent .container-fluid {
  padding-left: 0px;
  padding-right: 0px; }

.BodyComponent .smallMultiplePlotsContainer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  margin-top: 30px; }

.BodyComponent .smallMultiplePlot {
  display: inline-block;
  width: 300px;
  padding: 30px; }

.BodyComponent .smallMultipleHeader {
  height: 2em; }

.BodyComponent .filtersRow {
  background-color: #f5f5f5;
  min-width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  color: #3b3b3c;
  font-size: 12px; }

.BodyComponent .yearsSlider .rc-slider-track {
  background-color: #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle {
  border: solid 2px #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle:hover {
  border-color: #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle:focus {
  box-shadow: 0 0 0 5px #f48320ad; }

.BodyComponent .yearsSlider .rc-slider-dot-active {
  border-color: #f48320; }

.BodyComponent .multiSelector {
  padding-bottom: 10px; }



.MapComponent {
  padding-left: 10px; }
  .MapComponent .MapComponentMap .mapboxgl-canvas {
    top: 0; }
  .MapComponent #legendContainer {
    height: 160px; }
  .MapComponent .map-overlay {
    font-size: 12px;
    position: relative;
    z-index: 1;
    width: 130px;
    top: 0;
    left: 0;
    padding: 10px;
    float: right; }
  .MapComponent .map-overlay .map-overlay-inner {
    background-color: #fff;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    padding: 10px;
    margin-bottom: 10px; }
  .MapComponent .map-overlay label {
    display: block;
    margin: 0 0 10px; }
  .MapComponent .map-overlay input {
    background-color: transparent;
    display: inline-block;
    width: 100%;
    position: relative;
    margin: 0;
    cursor: ew-resize; }

